import React from 'react';
import Title from "../../../components/title";
import dayjs from "dayjs";
import {get} from "lodash";
import {useGetAllQuery} from "../../../hooks/api";
import {URLS} from "../../../constants/urls";
import {OverlayLoader} from "../../../components/loader";
import {useTranslation} from "react-i18next";
import Pdf from "../../../components/pdf";
import {PDFViewer} from '@react-pdf/renderer';
import Section from "../../../components/section";

const InpatientReportsByOrganizationsForm066ViewContainer = ({
                                                                 year = parseInt(dayjs().format('YYYY')),
                                                                 month = dayjs().format('MM'),
                                                                 form66_id
                                                             }) => {
    const {t} = useTranslation()
    const {data, isLoading} = useGetAllQuery({url: URLS.inpatientReports + `/${year}${month}/forms066/${form66_id}`})
    if (isLoading) {
        return <OverlayLoader/>
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-12">
                    <Title>{t('Форма 066')}</Title>
                </div>
                <div className="col-span-12 mt-8">
                    <Section>
                        <PDFViewer className={'w-full h-screen'}>
                            <Pdf data={get(data,'data')}/>
                        </PDFViewer>
                    </Section>
                </div>
            </div>
        </div>
    );
};

export default InpatientReportsByOrganizationsForm066ViewContainer;