import React from 'react';
import {get} from "lodash";
import {Document, Page, Text, View, StyleSheet, Font} from "@react-pdf/renderer";
import times from "../../assets/fonts/times/times.ttf"

Font.register({
    family: "Times-R",
    src: times
});
const styles = StyleSheet.create({
    page: {
        backgroundColor: 'white',
        flex: 1,
        flexDirection: 'column',
        padding: '15 15 15 15',
    },
    section: {
        flex: 1,
        flexDirection: 'row',
        alignItems:'flex-start'
    },
    text: {
        xxs: {fontSize: 8},
        xs: {fontSize: 10},
        sm: {fontSize: 12},
        base: {fontSize: 14},
        lg: {fontSize: 16},
        xl: {fontSize: 18},
        xxl: {fontSize: 24},
    },
    font: {
        times: {
            fontFamily: "Times-R",
        },
        weight:{
            bold:{
                fontWeight:'bold'
            }
        }
    },
   box:{
       border: '1px solid #000',
       padding:'1.5px 3px',
       marginRight:5
   },
    flex:{
        between:{
            justifyContent:'space-between'
        }
    },
    borderBottom: '1px solid #000',
    width:{
       25:{
           width:'25%'
       },
        40:{
            width:'40%'
        },
        50:{
           width: '50%'
        },
        60:{
            width: '60%'
        }
    }
});
const Index = ({data}) => {
    return (
        <>
            <Document>
                <Page size="A4" style={[styles.page, styles.font.times, styles.text.base]}>
                    <View style={[styles.section,styles.flex.between]}>
                        <View style={[styles.borderBottom,styles.section,styles.width["40"]]}>
                            <View style={[styles.box]}><Text>{get(data,'id')}</Text></View>
                            <View>
                                <Text style={styles.text}><Text style={[styles.text.xs]}>0.1.</Text>Муассаса коди ва <Text
                                    style={[styles.text.xs]}>0.1а</Text> номи:</Text>
                            </View>
                        </View>
                        <View style={styles.width["50"]}>
                            <Text>Ўзбекистон Республикаси Соғлиқни сақлаш вазирининг
                                2021й. ____ _________даги ____- сонли буйруғи билан
                                тасдиқланган <Text style={styles.font.weight.bold}>066-рақамли</Text> тибиий ҳужжат шакли.</Text>
                        </View>
                    </View>
                </Page>
            </Document>
        </>
    );
};

export default Index;